
export default function FancyCard(props){
    return (
        <div className="box">
            <img className="icon" src={props.image} alt={props.title + " logo"} />
            <span className="title">{props.title}</span>
            <style jsx>{`
                .box{
                    width: 320px;
                    height: 200px;
                    background: #ffff00;
                    ${props.css}
                    border-radius: 20px;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    margin: 20px;
                    flex-grow: 1;
                }
                .icon {
                    height: 90px;
                    width: auto;
                    margin: 16px auto;
                    max-width: 70%;
                    object-fit: contain;
                }
                .title{
                    font-size: 1.3em;
                }
            `}</style>
        </div>
    );
}